$colors: (
   'black': #000000,
   'white': #ffffff,

   'aqua': #00ffff,
   'blue': #0000ff,
   'green': #00ff00,
   'magenta': #ff00ff,
   'red': #ff0000,
   'yellow': #ffff00,

   'ao-english': #008000,
   'azure': #0080ff,
   'chartreuse-web': #80ff00,
   'electric-blue': #80ffff,
   'gray-web': #808080,
   'laser-lemon': #ffff80,
   'light-coral': #ff8080,
   'maroon-web': #800000,
   'medium-slate-blue': #8080ff,
   'navy-blue': #000080,
   'olive': #808000,
   'orange': #ff8000,
   'patriarch': #800080,
   'pink-flamingo': #ff80ff,
   'rose': #ff0080,
   'screamin-green': #80ff80,
   'spring-green': #00ff80,
   'teal': #008080,
   'violet-color-wheel': #8000ff,

   'cultured': #f0f0f0,
   'smoky-black': #0f0f0f,

   'eerie-black': #1c1c1c,
   'jet': #393939,
   'davys-grey': #555555,
   'sonic-silver': #717171,
   'spanish-gray': #8e8e8e,
   'silver-chalice': #aaaaaa,
   'silver': #c6c6c6,
   'platinum': #e3e3e3,
);

@function color($color) {
   @return map-get($colors, $color);
}

@each $color, $value in $colors {
   @include ss($color) {
      color: $value;
   }

   @include ss('bg_#{$color}') {
      background-color: $value;
   }
}